// web fonts

// Nunito --------------------------------------------------------------

$base_url: "/hyllo_site";

@font-face {
  font-family: "Nunito";
  src: url($base_url + "/assets/fonts/Nunito/Nunito-Regular.ttf");
  font-weight: normal;
  font-style: normal;
};

@font-face {
  font-family: "Nunito";
  src: url($base_url +  "/assets/fonts/Nunito/Nunito-Italic.ttf");
  font-weight: normal;
  font-style: italic;
};

@font-face {
  font-family: "Nunito";
  src: url($base_url + "/assets/fonts/Nunito/Nunito-Light.ttf");
  font-weight: lighter;
  font-style: normal;
};

@font-face {
  font-family: "Nunito";
  src: url($base_url + "/assets/fonts/Nunito/Nunito-LightItalic.ttf");
  font-weight: lighter;
  font-style: italic;
};

@font-face {
  font-family: "Nunito";
  src: url($base_url + "/assets/fonts/Nunito/Nunito-Bold.ttf");
  font-weight: bold;
  font-style: normal;
};

@font-face {
  font-family: "Nunito";
  src: url($base_url + "/assets/fonts/Nunito/Nunito-BoldItalic.ttf");
  font-weight: bold;
  font-style: italic;
};

@font-face {
  font-family: "Nunito";
  src: url($base_url + "/assets/fonts/Nunito/Nunito-SemiBold.ttf");
  font-weight: 600;
  font-style: normal;
};